	margin-left: 64px;
	padding: 4px;
}
.option ul {
	padding: 0;
}
.option ul> li {
	float: left;
	list-style: none;
	margin-right: 4px;
	padding: 0 8px;
	border: 2px solid #eee;
}
.option ul> li:hover {
	border: 2px solid #c0392b;
}
.option ul .active {
	border: 2px solid #c0392b;
}



