
.cart ul {
  padding: 0;
}
.cart ul> li {
  list-style: none;
  padding: 4px 8px;
}
.popover {
  min-width: 320px;
}
.cart-empty {
  padding: 100px;
  border-radius: 4px;
  border: 2px dashed rgb(170, 170, 170);
}
#content {
  top: 50%;
  width: 100%;
  text-align: center;
  left: 0;
}



